.site-container {
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;

  display: flex;
  flex-direction: column;
  font-family: Poppins, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;

  min-height: 100vh;
  /* BOX MODEL */
  width: 100%;
  height: auto;
  border: 1px solid black;

  /* ENHANCEMENT */
  background-image: url("./assets/backgrounds/karim-ben-van-mKk2nnZ1EQk-unsplash.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}
.site-content {
  display: flex;
  justify-content: center;
}
/* body {
  background-image: url("./assets/backgrounds/1.jpg");
  min-height: 100vh;
}
main {
  min-height: 100vh;
} */
footer {
  background-color: darkolivegreen;
  margin-bottom: 0px;
  padding: 10px;
  opacity: 0.7;
  max-height: 50px;
  display: flex;
  align-items: end;
  justify-content: center;
  margin-top: auto;
}
html {
  margin: 0;
  padding: 0;
}
h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Abril Fatface", serif;
  font-size: 30px;
  color: rgb(97, 71, 57);
  text-shadow: 2px 2px #fef2cd;
}
h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Abril Fatface", serif;
  font-size: 23px;
  color: rgb(97, 71, 57);
}
h3 {
  font-family: "Abril Fatface", serif;
  color: rgb(65, 55, 31);

  /*Home page*/
}
#home {
  margin: 200px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
#home h1 {
  text-align: center;
  font-size: 4em;
  padding: 0;
  margin-bottom: 1rem;
}
#home h2 {
  text-align: center;
  font-size: 2em;
  text-shadow: 2px 2px #ffebc5;
}
#lia {
  padding: 5px;
  opacity: 0.85;
  width: 100%;
  display: flex;
  justify-content: center;
}
#home h3 {
  color: rgb(69, 60, 38);
  text-align: center;
  font-size: 1.5em;
  font-family: Poppins, sans-serif;
  padding: 10px;
  background-color: rgb(255, 239, 191);
  border-radius: 15%;
}
/* About me - page */
.about-me-content {
  display: flex;
  justify-content: center;
  margin: 20px;
}
.about-me img {
  width: 500px;
  height: 600px;
  margin: 5px;
  padding: 0px;
  border-radius: 10%;
}
.about-me {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 75px;
  background-color: #fff6e2;
  padding: 30px;
  margin: 10px;
  border-radius: 10%;
  width: 1000px;
  height: auto;
}
.tex-about-me {
  margin: 20px;
  padding: auto;
  display: flex;
  flex-wrap: wrap;
}
.cv-aside img {
  width: 80%;
  height: auto;
  margin: 5px;
  padding: 5px;
  border: 5px solid rgb(65, 55, 31);
  border-radius: 50%;
}
.CV-content {
  display: flex;
  justify-content: center;
  margin-bottom: 100px;
  margin-top: 40px;
  max-width: 1900px;
}
.CV-content h1 {
  text-shadow: none;
}
.cv-aside {
  background-color: beige;
  min-width: 200px;
  width: 20%;
  padding: 15px;
  margin-left: 15%;
  border: 2px solid rgb(65, 55, 31);
}
.cv-list-title {
  border-bottom: 2px solid rgb(65, 55, 31);
}
.work-education h3 {
  border-bottom: 3px solid rgb(65, 55, 31);
}
.work-education {
  background-color: rgb(254, 254, 246);
  min-width: 400px;
  width: 40%;
  padding-right: 20px;
  margin-right: 15%;
  border-top: 2px solid rgb(65, 55, 31);
  border-bottom: 2px solid rgb(65, 55, 31);
  border-right: 2px solid rgb(65, 55, 31);
}
.work-education ul {
  list-style: none;
}
.work-education-description {
  border-bottom: 2px solid rgb(80, 118, 76);
  opacity: 0.7;
}
.date {
  padding-right: 30px;
  text-align: right;
  width: 30%;
  float: right;
  font-size: smaller;
}
.contact-form {
  margin-top: 50px;
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
form {
  margin: 20px;
  padding: 20px;
  width: 600px;
  height: auto;
  border: 1px solid #543f3f;
  border-radius: 30px;
  background-color: #fff6e2;
}
label {
  display: block;
  margin-top: auto;
  margin-bottom: auto;
  margin-top: 25px;
}
input[type="submit"] {
  background-color: rgb(255, 255, 255);
  padding: 6px 10px;
  cursor: pointer;
  border-radius: 10%;
}
input[type="submit"]:hover {
  background-color: rgb(80, 118, 76);
}

/*Portfolio page*/

@media screen and (max-width: 1000px) {
  .about-me {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 75px;
    background-color: #fff6e2;
    padding: 10px;
    margin: 0;
    border-radius: 5%;
    width: 100%;
    height: auto;
  }
  .about-me img {
    width: 85%;
    height: auto;
    margin: 5px;
    padding: 0px;
    border-radius: 10%;
  }
}

/* css when screen is max 700px wide */
@media screen and (max-width: 700px) {
  .contact {
    display: flex;
    justify-content: center;
    margin: 20px;
    padding: 20px;
    width: 85%;
    height: auto;
    border: 1px solid #543f3f;
    border-radius: 30px;
    background-color: #fff6e2;
  }
  .date {
    float: left;
    text-align: left;
    width: 100%;
  }
  .CV-content {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column-reverse;
    width: 100%;
    padding: 0;
    align-items: center;
  }
  .cv-aside {
    min-width: 400px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
    margin-left: 0;
    margin-right: 0;
    border-bottom: 2px solid rgb(65, 55, 31);
    border-left: 2px solid rgb(65, 55, 31);
  }
  .work-education {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
    margin-top: 40px;
    margin-left: 0;
    margin-right: 0;
    padding-right: 0;
    border-bottom: 0;
    border-left: 2px solid rgb(65, 55, 31);
    padding: 15px;
  }
  #home {
    margin: 200px;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  #home h1 {
    font-size: 2.5em;
  }
  #home h2 {
    font-size: 1.5em;
  }
  #home h3 {
    font-size: 1em;
  }
  #home {
    margin: 40px;
  }
  .contact-container {
    min-width: 400px;
  }
  /* For printing */
}
@media print {
  body {
    color: #000000;
    background-color: #ffffff;
    font-family: Georgia, Times, serif;
  }
  p {
    font-family: Georgia, Times, serif;
    font-size: 12pt;
  }
  .CV-content {
    font-family: Georgia, Times, serif;
  }
  h1 {
    font-family: Georgia, Times, serif;
  }
  h3 {
    font-family: Georgia, Times, serif;
  }
  .about-me-content {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  .about-me-content p {
    font-size: 14pt;
  }
  a:link {
    color: #0000cc;
    text-decoration: underline;
  }
  footer {
    display: none;
  }
  header {
    display: none;
  }
}
